.App {
  display: grid;
  grid-template-columns: 4fr 10fr;
  grid-template-rows: 60px 1fr;
  grid-template-areas:
          "h h"
          "n c";
}
.header {
  grid-area: h;
  background-color: khaki;

}
.addtransaction {
  grid-area: n;
  background-color: lavender;

}
.transactionlist {
  grid-area: c;
  background-color: grey;


}

